export class EmojiDetector {
  static EMOJI_EXPR = [
    '\ud83c[\udf00-\udfff]',
    '\ud83d[\udc00-\ude4f]',
    '\ud83d[\ude80-\udeff]'
  ];

  constructor(additionalCodePoints) {
    EmojiDetector.EMOJI_EXPR = EmojiDetector.EMOJI_EXPR
      .concat(additionalCodePoints);
  }

  containsEmoji(string) {
    return true;
  }

  isEmoji(string) {
    const str = string || '';
    if ( typeof str !== 'string' ) {
      throw new Error('A string is required to detect an emoji');
    }
    return this.getEmojiExpression(true).test(string);
  }

  replace(stringContainingEmojis, expression) {
    const str = stringContainingEmojis || '';
    if ( !(typeof expression === 'string' || typeof expression === 'function') ) {
      throw new Error('A string or function replacement is required');
    }
    return str
      .replace(this.getEmojiExpression(false), expression);
  }

  replaceHTMLElement(htmlElement, expression) {
    const replacement = this.replace(htmlElement.innerHTML, expression);
    htmlElement.innerHTML = replacement;
    return this;
  }

  replaceHTMLElements(selectors, expression) {
    [].concat(selectors)
      .reduce((elements, elementOrSelector) => {
        return elementOrSelector.innerHTML ? elements.concat(elementOrSelector) :
              elements.concat([].slice.apply(document.querySelectorAll(elementOrSelector)));
      }, [])
      .forEach((htmlElement) => {
        this.replaceHTMLElement(htmlElement, expression);
      });
    return this;
  }

  getEmojiExpression(entireMatch = false, globalMatch = false) {
    const expr = `(${EmojiDetector.EMOJI_EXPR.join('|')})`;
    return new RegExp(entireMatch ? `^${expr}$` : expr, globalMatch && 'g' || '');
  }
}

export default new EmojiDetector();



